import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, NavParams } from 'ionic-angular';
import { ClinicMapper } from '../../models/clinic.model';
import { ClinicsProvider } from '../../providers/clinics/clinics';
import { ClinicDetailPage } from '../index';

/**
 * Generated class for the ClinicsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-clinics',
  templateUrl: 'clinics.html',
})
export class ClinicsPage {

  public searching: boolean = false;

  public currentItems: ClinicMapper[];

  constructor(public navCtrl: NavController, public navParams: NavParams,
              private clinicsPrv: ClinicsProvider, private modalCtrl: ModalController) {
   this.getItems();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ClinicsPage');
  }

  getItems() {
    this.searching = true;
    this.clinicsPrv.query().subscribe((data: ClinicMapper[]) => {
      this.searching = false;
      this.currentItems = data;
    });
  }

  addItem() {
    let detailModal = this.modalCtrl.create(ClinicDetailPage);
    detailModal.onDidDismiss((data: any) => {
      if (data.reload) {
        this.getItems()
      }
    });
    detailModal.present();
  }

  openItem(item: ClinicMapper) {
    let detailModal = this.modalCtrl.create(ClinicDetailPage, { item: item });
    detailModal.onDidDismiss((data: any) => {
      if (data.reload) {
        this.getItems();
      }
    });
    detailModal.present();
  }
}



